import React, { useState , useEffect} from 'react'
import './Footer.css';
const Footer = () => {
    const [year, setYear] = useState(null);
    useEffect(() => {
        let y = new Date();
        setYear(y.getFullYear())
    }, [])
    return (
        <footer>
            Made with React @{year}
        </footer>
            
    )
}

export default Footer
