import React from 'react'
import './Skills.css';
import {my_skills} from './my_skills';
import a from './A.png';
import css from './CSS.png';
import r from './R.png';
import h from './HTML.png';
import j from './JS.png';
import wp from './WP.png';
import php from './PHP.png';
const images = [h,css,j,a,r,wp,php];
const Skills = () => {
    return (
        <section className='skills' id='skills'>
            <h2>SKILLS</h2>
            <div className="skills-wrap">
            {my_skills.map((item,index)=>{
                return (
                    <div className="skill-card" key={index}>
                        <img src={images[index]} alt='' className="skill-shorthand"/> 
                        {/* <span className="skill-shorthand">{item.shorthand}</span> */}
                        <div className="skill-title">{item.title}</div>
                        <div className="skill-description">{item.short_desc}</div>
                    </div>  
                )
            })}
            </div>    
        </section>
    )
}

export default Skills
