import React from 'react'
import './Hero.css';
import { about } from './about';
import myself from './me.png';
const Hero = () => {
    return (
        <section className='hero' id='about_me'>
            <div className="hero-wrap">
                <img src={myself} alt=''/>
                <div className="name">{about.name}</div>
                <div className="job">{about.job}</div>
                <ul className="socials">
                    {about.social_links.map((item,index)=>{
                        return (
                            <li key={index}>
                                <a href={item.link}>
                                    <i className={item.className}></i>
                                </a>
                            </li>
                        )
                    })}
                </ul>
            </div>
        </section>
    )
}

export default Hero
