import React, { useRef } from 'react'
import { links } from './header-links';
import './Header.css';
import AnchorLink from 'react-anchor-link-smooth-scroll'
const Header = () => {
    const burgerRef = useRef(null);
    const linksRef = useRef(null);
    const openMenu = () =>{
        burgerRef.current.classList.toggle("active");
        linksRef.current.classList.toggle("active");
    }
    return (
        <header>
            <div className="burger" onClick={openMenu} ref={burgerRef}>
                <div className="line line1"></div>
                <div className="line line2"></div>
                <div className="line line3"></div>
            </div>
            <ul className='anchor-link-list' ref={linksRef}>
            {links.map(( link, index)=>{
                return <li key={index} className='anchor-link-wrap'>
                        <AnchorLink href={link.link} offset='100' className='anchor-link-item' onClick={()=>{linksRef.current.classList.toggle('active')}}>{link.text}</AnchorLink>
                    {/* <a href={`#${link.link}`} >{link.text}</a> */}
                        </li>
            })}
            </ul>
        </header>
        
    )
}

export default Header
