import React, { useState } from 'react'
import { proj } from './projects_list';
import './Projects.css';
const filter_buttons = ["All", "Commercial", ...new Set(proj.map((p) => p.coding_lang))];


const Projects = () => {
    const [projects, setProjects] = useState(proj);
    const [i, setI] = useState(0);
    const filter_Projects = (e,i) =>{
        let query = e.target.innerText;
        let filtered;
        setI(i);
        if(query === "All"){
            setProjects(proj);
        }else if(query === "Commercial"){
            filtered = proj.filter((p)=> p.commercial === true);
            setProjects(filtered);
        }else{
            filtered = proj.filter((p)=> p.coding_lang === query);
            setProjects(filtered)
        }
    }   
    return (
        <div id='projects'>
            <h2>Projects</h2>
            <div className="filter">
                {filter_buttons.map((b,index)=>{
                    return <button key={index} onClick={(e)=>filter_Projects(e,index)} className={i === index ? 'btn active' : 'btn'}>{b}</button>
                })}
            </div>
            <div className="projects_wrap">
                {projects.map((item,index)=>{
                    return (
                        <div className="project_item" key={index}>
                            <img src={item.img} alt="project_image" className='project_image' />
                            <div className="project_description_wrap">
                                <div className="project_title">{item.title}</div>
                                <span className="project_lang">{item.coding_lang}</span>
                                <div className="project_about">{item.about}</div>
                                <div className="project_links">
                                    {item.code_link && <a href={item.code_link}>Check the code</a>}
                                    <a href={item.site_link}>Visit website</a>
                                </div>
                            </div>
                        </div>
                    )
                })}
                
            </div>
        </div>
    )
}

export default Projects
